<template>
  <div class="liItem">
    <div class="back" @click="back"></div>
    <ul>
      <li
        v-for="(item, index) in songlist"
        :key="index"
      >
        <div class="left">
          <span class="num">{{
            index + 1 > 9 ? index + 1 : "0" + (index + 1)
          }}</span>
        </div>
        <div class="center">
          <p class="song_name">
            {{ item.name }}
            <span v-if="item.song.alias.length != 0" class="tip"
              >({{ item.song.alias[0] }})</span
            >
          </p>
          <p class="songer">
            <i v-if="item.song.privilege.playMaxbr == 999000"></i>
            {{ item.song.artists[0].name }}-{{ item.name }}
          </p>
        </div>
        <div class="right" @click="$emit('play', item)"></div>
      </li>
    </ul>
    <div class="more" @click="get">{{ hasmore ? '加载更多>>>' : '人家是有底线的···'}}</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      allsonglist: [],
      songlist:[],
      limit:1,
      hasmore:true,
    };
  },
  methods:{
      back(){
          this.$router.back()
      },
      get(){
          this.limit++;
          if(this.limit > 5){
            this.hasmore = false;
          }
          this.songlist = this.allsonglist.slice(0,(this.limit * 20));
        },
      },
      created() {
        this.$axios.get('/personalized/newsong?limit=100').then(d=>{
            // console.log(d.data.result);
            this.allsonglist=d.data.result;
            this.songlist = this.allsonglist.slice(0,20);
        });
      },
};
</script>
<style lang="less" scoped>
.liItem{
  width: 100%;
  height: 93vh;
  overflow-y: scroll;
.back{
    position: fixed;
    top: 100px;
    left: 0;
    width: 0.8rem;
    height: 0.8rem;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 0.8rem;
    z-index: 10;
  }
  .more{
    font-size: 16px;
    color: #aaa;
  }
ul {
  width: 100%;
  padding: 5px 0px;
  box-sizing: border-box;
  margin-top: 15px;
  li {
    border-bottom: 1px solid #f0f0f1;
    position: relative;
    width: 100%;
    height: 55px;
    display: flex;
    // flex-direction: column;
    justify-content: flex-start;
    // align-items:center;
    overflow: hidden;
    padding-left: 10px;
    .left {
      width: 30px;
      margin-right: 10px;
      line-height: 55px;
      .num {
        display: inline-block;
        font-size: 20px;
        color: #ccc;
        width: 30px;
      }
    }
    .center {
        width: 80%;
        display: flex;
        flex-direction: column;
        justify-content: center;
      .song_name {
        width: 80%;
        height: 20px;
        font-size: 16px;
        color: #333;
        text-align: left;
        text-overflow: ellipsis;
        overflow: hidden;
        .tip {
          height: 25px;
          color: #888;
          margin-left: 4px;
        }
      }
      .songer {
        width: 280px;
        height: 16px;
        text-overflow: ellipsis;
        overflow: hidden;
        text-align: left;
        font-size: 12px;
        color: #888;
        i {
          display: inline-block;
          width: 12px;
          height: 8px;
          margin-right: 4px;
          background: url('../assets/index_icon_2x.png')
            no-repeat;
          background-size: 166px 97px;
        }
      }
    }
    .right{
        width: 22px;
        height: 22px;
        position: absolute;
        right: 20px;
        top: 50%;
        margin-top: -11px;
        background: url(../assets/index_icon_2x.31e6f788.png) no-repeat;
        background-size: 166px 97px;
        background-position: -24px 0;
        cursor: pointer;
    }
  }
}
}
</style>